import React from "react";
import DefaultLayout from '../layouts/DefaultLayout';
import {
    Container,
    Row,
    Col,
    Alert,
    Card,
    CardDeck,
    CardBody,
    CardTitle,
    CardText
} from 'reactstrap';

const FeatureSectionTitle = ({ children }) => <span className='eolm-feature-title'>{children}</span>

export default () => (
    <DefaultLayout>
        <Container>
            <Row>
                <div className='col'>
                    <h3 className='text-right mt-2'>A handy library for an easy use of openlayers.</h3>
                    <h4>Motivation</h4>
                    <p>
                        Openlayers is awesome, very flexible and customizable, but some of its functionalities
                        are not straightforward.
                        So, we wanted to make it easy, have a library that allows you to use some openlayers functionality out of the box
                        and that easily integrates with native openlayers-based code. Also, we added some functionality
                        that leverages the WMS, WFS and WPS Standards capabilities.
                    </p>
                    <Alert color='info'>
                        <strong>Note:</strong> WMS, WFS and WPS related functionality was built and tested on <strong>GeoServer</strong> implementations, so it is intended to be used along with it.
                    </Alert>
                    <h4>Features</h4>
                    <CardDeck>
                        <Card>
                            <CardBody>
                                <CardTitle><FeatureSectionTitle>OGC Standards-based Tiled Layers</FeatureSectionTitle></CardTitle>
                                <CardText>
                                    Get a WMS-based tiled layer that you can easily add to your map, and take advantage of functionality
                                    built on WMS, WFS and WPS capabilities.
                                </CardText>
                            </CardBody>
                        </Card>
                        <Card>
                            <CardBody>
                                <CardTitle><FeatureSectionTitle>Easy styling</FeatureSectionTitle></CardTitle>
                                <CardText>
                                    Style your features in an easy way, just with a single set of properties.
                                </CardText>
                            </CardBody>
                        </Card>
                    </CardDeck>
                    <CardDeck className="my-4">
                        <Card>
                            <CardBody>
                                <CardTitle><FeatureSectionTitle>Easy drawing</FeatureSectionTitle></CardTitle>
                                <CardText>
                                    Get drawing quickly, just specify what you want to draw and where,
                                    give some easy styling and start drawing!
                            </CardText>
                            </CardBody>
                        </Card>
                        <Card>
                            <CardBody>
                                <CardTitle><FeatureSectionTitle>Easy Integration</FeatureSectionTitle></CardTitle>
                                <CardText>
                                    Don't worry about integration. Easyolmaps naturally integrates with
                                    existing openlayers-based code.
                            </CardText>
                            </CardBody>
                        </Card>
                    </CardDeck>
                    <h4>Get Involved</h4>
                    <CardDeck>
                        <Card style={{ maxWidth: '320px' }}>
                            <a href="https://github.com/henryvanner/easyolmaps/issues"
                                className='card-body eolm-card-link p-2 m-0'
                            >
                                <h4 className='m-0'><i className='fas fa-bug mr-2'></i>Report an issue</h4>
                            </a>
                        </Card>
                    </CardDeck>
                </div>
            </Row>
        </Container>
    </DefaultLayout>
)