import React from "react";
import {
    Navbar,
    Nav,
    NavItem,

} from 'reactstrap';
import { Link } from 'gatsby';

const NavLink = ({ to, children }) => (
    <Link to={to} className='nav-link'>{children}</Link>
);

const ExternalLink = ({ to, children }) => (
    <a href={to} className='nav-link'>{children}</a>
);

const DefaultHeader = () => (
    <Navbar color="dark" dark={true} expand="lg" fixed='top'>
        <Link className='navbar-brand mr-auto' to="/" style={{ color: 'white', textDecoration: 'none' }}>EasyOlMaps</Link>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#defaultNavbar" aria-controls="defaultNavbar" aria-expanded="false">
            <span className="navbar-toggler-icon"></span>
        </button>
        <div className='collapse navbar-collapse' id="defaultNavbar">
            <Nav className="ml-auto" navbar>
                <NavItem>
                    <NavLink to='/examples'>Examples</NavLink>
                </NavItem>
                <NavItem>
                    <NavLink to='/apidoc/layer/GeoserverLayer'>API</NavLink>
                </NavItem>
                <NavItem>
                    <ExternalLink to='https://github.com/henryvanner/easyolmaps'>Code</ExternalLink>
                </NavItem>
            </Nav>
        </div>
    </Navbar>
);

export default ({ children }) => (
    <React.Fragment>
        <DefaultHeader />
        {children}
    </React.Fragment>
)